.section1 {
  display: flex;
  flex-direction: column;
  gap: 1rem;
 
  width: 100%;
  position: relative;
  align-items: center;

  &__video__container{
    content: '';
    position: relative;
    height: fit-content;
    width: fit-content;
    background-color: rgb(0, 0, 0);
    z-index: 6;
  
    height: 100%;
  }
  &__video {
    width: auto;
    z-index: 4;
    max-width: 100%;
    pointer-events: none;
    object-fit: fill;
    object-position: center;
    opacity: 0.4;
  }

  &__upper_text__wrapper{
    position: absolute;
    top: 0;
    left:0;
    z-index: 10;
    padding: 1rem;
    display: flex;
    flex-direction: column;
  }
  &__lower_text__wrapper {
    position: absolute;
    bottom: 0;
    left:50%;
    padding: 1rem;
    border:  1px dotted var(--main-text-color);
    border-radius: 10px;
    opacity: 0.8;
    transform:translate(-50%, -50%);
    cursor: pointer;
      &:hover {
        text-decoration: underline var(--main-text-color);
      }
  }

  &__text {
    animation: textShine 3s ease-in-out infinite alternate;
    -webkit-text-fill-color: transparent;
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: var(--text-gradient);
    background-size: 300% auto;
    
    font-size: 42px;
    letter-spacing: 4px;
    @keyframes textShine {
      0% {
          background-position: 0% 50%;
      }
      100% {
          background-position: 100% 50%;
      }
    }
  }
  &__text__lower {
    animation: textShine 3s ease-in-out infinite alternate;
    -webkit-text-fill-color: transparent;
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: var(--text-gradient);
    background-size: 300% auto;
    font-size: 32px;
    letter-spacing: 4px;
    @keyframes textShine {
      0% {
          background-position: 0% 50%;
      }
      100% {
          background-position: 100% 50%;
      }
    }
  }

  &__text__bold {
    font-weight: 700;
    animation: textShine 3s ease-in-out infinite alternate;
    -webkit-text-fill-color: transparent;
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: var(--text-gradient);
    background-size: 300% auto;
    font-size: 60px;
    letter-spacing: 8px;
    margin-bottom: 1rem;
    
    @keyframes textShine {
      0% {
          background-position: 0% 50%;
      }
      100% {
          background-position: 100% 50%;
      }
    }
  }

  &__bottom_text__wrapper{
    border-top: 2px solid var(--main-text-color)
  }
  @media(max-width: 480px) and (min-width:290px) {
    .section1{
     

      &__video__container {
        margin-bottom: 6rem;
      }
      &__video {
        max-width: 100%;
        height: 100%;
      }
      &__text {
        font-size: 30px;
      }
      &__text__bold{
        font-size: 34px;
      }
      &__text__lower{
        font-size: 12px;
      }
      &__lower_text__wrapper {
        padding: 5px;
        bottom: 4rem;
      }
    }
  }
}

@media(max-width: 280px) {
  .section1{ 
    &__text {
      font-size: 20px;
    }
    &__text__bold{
      font-size: 27px;
    }
}
}

@media(max-width: 926px) and (min-width: 750px) {
  .section1{ 
    &__text {
      font-size: 20px;
    }
    &__text__bold{
      font-size: 27px;
    }
    &__text__lower{
      font-size: 12px;
    }
}
}